body {
  background: #1c232e;
  font-size: 18px;
  font-family: sans-serif, Arial, Helvetica;
}

.container {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}

textarea {
  background: #14181f;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  outline: none;
  border: none;
  border-radius: 5px;
  padding: 20px;
  font-size: 22px;
  color: white;
  resize: none;
}

.preview {
  padding: 20px;
}

.preview * {
  color: white;
}
